import React from "react"
import { Link, graphql } from "gatsby"
import Layout from "../components/Layout"
import AddToCartButton from "../components/AddtoCartButton"
import AddToSnipCart from "../components/AddToSnipCart"

export default function Home({ data }) {
  const products = data.products.nodes
  const categories = data.categories.nodes

  return (
    <Layout>
      <h2>
        <mark>(Product Categories)</mark>
      </h2>
      <div>
        {categories &&
          categories.map(category => (
            <div key={category.id}>
              <Link
                to={`/product-category/${category.slug}`}
                style={{ color: "inherit" }}
              >
                <h2>{category.name}</h2>
              </Link>
              <h4>{category.count}</h4>
            </div>
          ))}
      </div>
      <button
        type="button"
        onClick={() => {
          const cart = JSON.parse(window.localStorage.getItem("wooCart"))
          const line_items = cart?.line_items?.map(item => {
            return { product_id: item.product_id, quantity: item.quantity }
          })
          const total = cart?.line_items?.reduce(
            (acc, item) => acc + item.quantity * item.price,
            0
          )
          console.log(line_items)
          console.log(total)
        }}
      >
        SENDING ORDER (kilogolja azt, ami a rendeléshez kell)
      </button>
      <h2>
        <mark>(Product Listing)</mark>
      </h2>
      {products.map(product => (
        <div key={product.id}>
          <Link to={`/product/${product.slug}`}>
            <h2>
              {product.name}
              {product.on_sale && <span>SALE!</span>}
            </h2>
          </Link>
          <Link to={`/product-category/${product.categories[0].slug}`}>
            <p>
              <strong>{product.categories[0].name}</strong>
            </p>
          </Link>
          <img src={product.images[0].src} alt="product" width="200" />
          <div
            dangerouslySetInnerHTML={{ __html: product.short_description }}
          />
          <h4>{product.price} €</h4>
          {/* <AddToCartButton product={product} /> */}
          <AddToSnipCart product={product} />
        </div>
      ))}
    </Layout>
  )
}

export const query = graphql`
  query {
    products: allWcProduct {
      nodes {
        id
        slug
        name
        on_sale
        total_sales
        catalog_visibility
        description
        short_description
        price
        regular_price
        sale_price
        average_rating
        categories {
          id
          name
          slug
        }
        images {
          src
          alt
        }
        wooCommerceId
        manage_stock
        stock_quantity
        stock_status
      }
      pageInfo {
        perPage
      }
    }
    categories: allWcCategory {
      nodes {
        id
        name
        wooCommerceId
        slug
        count
        image {
          src
          name
        }
      }
    }
  }
`
