import React from "react"
import { graphql, Link } from "gatsby"
import Layout from "../components/Layout"

export default function CategoryPage({ data }) {
  const products = data.productsInCategory.edges
  const category = data.category

  return (
    <Layout>
      <div>
        <img src={category.image.src} alt={category.name} />
      </div>
      <h1>{category.name}</h1>
      <p>
        <mark>category.count</mark>
      </p>
      <small>
        {category.count} art in {category.name}
      </small>
      <p>
        <mark>Category description, if any:</mark>
      </p>
      <div
        dangerouslySetInnerHTML={{
          __html: category.description || `<p>no description found</p>`,
        }}
      />
      <h2>
        <mark>(Products in this category)</mark>
      </h2>
      {products &&
        products.map(product => (
          <div key={product.node.id}>
            <Link to={`/product/${product.node.slug}`}>
              <h2>{product.node.name}</h2>
            </Link>
            <div
              dangerouslySetInnerHTML={{ __html: product.node.price_html }}
            />
            <div
              dangerouslySetInnerHTML={{
                __html: product.node.short_description,
              }}
            />
          </div>
        ))}
    </Layout>
  )
}

// This needs to be dynamic based on the slug passed in via context in gatsby-node.js
export const query = graphql`
  query($categoryRegex: String!, $id: String!) {
    productsInCategory: allWcProduct(
      filter: { categories: { elemMatch: { name: { regex: $categoryRegex } } } }
    ) {
      edges {
        node {
          name
          id
          slug
          short_description
          on_sale
          images {
            name
            src
          }
          price_html
        }
      }
    }
    category: wcCategory(id: { eq: $id }) {
      id
      name
      count
      slug
      description
      image {
        name
        src
        alt
      }
      wooCommerceId
    }
  }
`
