import React from "react"
import { toast } from "react-toastify"
import { addToCart } from "../functions/addToCart"
import { useDispatch } from "react-redux"
import { loadCart, openCart } from "../cartSlice"

export default function AddToCartButton({ product }) {
  const dispatch = useDispatch()
  const notify = name => {
    toast(`🎉 ${name} was added to cart!`)
  }

  const handleAddToCart = product => {
    addToCart(product)
    // notify(product.name)
    try {
      const newCart = JSON.parse(window.localStorage.getItem("wooCart"))
      const items = newCart.line_items
      dispatch(loadCart(items))
      dispatch(openCart())
    } catch (e) {
      console.log(e)
    }
  }

  return (
    <>
      {product.stock_quantity < 1 ? (
        <p>Out of stock.</p>
      ) : (
        <a
          role="button"
          tabIndex={0}
          type="button"
          onKeyDown={addToCart}
          onClick={() => handleAddToCart(product)}
        >
          Add to Cart
        </a>
      )}
    </>
  )
}
